import { useState, useEffect } from 'react';
import { Grid, Button, NoticeBar, Dialog } from 'antd-mobile';
import { 
    CameraOutline, UploadOutline, CloseOutline, 
    SmileOutline, ArrowsAltOutline, ShrinkOutline 
} from 'antd-mobile-icons';
import { isMobile } from 'react-device-detect';

import {MqttClient} from './common/MqttClient';
import { BROKER } from './common/Constants';
import { aesEncrypt } from './common/Utils';

import CameraView from './CameraView';
import ImageView from './ImageView';

import logo from './Logo_Munisys.png';
import './DocumentCaptureView.css';
import { uuidv7 } from 'uuidv7';

const PROCESS_STEP_IMAGE_CAPTURING = "IMAGE_CAPTURING";
const PROCESS_STEP_IMAGE_UPLOADING = "IMAGE_UPLOADING";

function DocumentCaptureView() {

    // let mqttClient = undefined;
    const [mqttClient, setMqttClient] = useState(undefined);
    const [topicStatusName, setTopicStatusName] = useState(undefined);
    const [topicDataName, setTopicDataName] = useState(undefined);
    const [encryptionKey, setEncryptionKey] = useState(undefined);
    const [brokerStatus, setBrokerStatus] = useState(0);
    const [processStep, setProcessStep] = useState(undefined); 
    const [imageData, setImageData] = useState(undefined); 
    const [uploadingImageData, setUploadingImageData] = useState(false); 
    const [fullscreen, setFullscreen] = useState(false);

    useEffect(() => {
        // console.log("I have been mounted [DocumentCaptureView]")
        const queryParameters = new URLSearchParams(window.location.search)
        const caller = queryParameters.get("caller");
        const token = queryParameters.get("token");
        const key = queryParameters.get("key");
        if(!caller || !token) 
            throw new Error("Paramétres d'entrée non trouvés: 'caller' et/ou 'token'");
        // console.log(caller, token, key);
        const topicPrefix = BROKER.TOPIC_PREFIX+'/'+caller+'/'+token;
        console.log("topicPrefix", topicPrefix);
        const _topicStatusName = topicPrefix + '/status';
        const _topicDataName = topicPrefix + '/data';
        setTopicStatusName(_topicStatusName);
        setTopicDataName(_topicDataName);
        setEncryptionKey(key);

        setTimeout(()=>initWebcam());
        setTimeout(()=>initBroker());

        return () => {
            disconnectBroker();
        }
    }, [])

    const initWebcam = () => {
        navigator.mediaDevices.getUserMedia({ video: { facingMode: { exact: (isMobile? "environment" : "user") } } })
            .then(function (stream) {
                stream.getTracks().forEach(function(track) {
                    track.stop();
                });
            }.bind(this))
            .catch(function (error) {
                console.error("ERROR ACCESSING VIDEO STREAM: ", error);
                Dialog.alert({
                    content: 'Erreur camera: ' + error.toString(),
                    onConfirm: () => {},
                })
            }.bind(this));
    }

    const initBroker = () => {
        try{
            if(mqttClient && mqttClient.isConnected) return;
            console.log("Initializing broker connection ...");
            const _mqttClient = mqttClient? mqttClient : new MqttClient();
            if(_mqttClient){
                _mqttClient.onConnectCallback = () => {
                  console.log("connected to broker");
                  setBrokerStatus(1);
                //   Dialog.confirm({
                //     content: "Voulez-vous activer le mode 'Plein écran'? (vous pouvez basculer à tout moment en cliquant sur le bouton en haut à droite de l'écran)",
                //     onConfirm: () => {
                //         goFullScreen();
                //     },
                //   });
                //   setTimeout(()=>{
                //     publishMessage(topicStatusName, "DEVICE_CONNECTED");
                //   }, 1000);
                }
                _mqttClient.onConnectionLost = (responseObject) => {
                    console.log('onConnectionLost', responseObject);
                    setBrokerStatus(0);
                }
                // _mqttClient.subscribe("/adiop/test",(topic,message)=>{        // FOR TESTING ...
                //     console.log("on msg received: ", topic, message);
                // });
                _mqttClient.onMessageArrived = (message) => {
                    console.log('onMessageArrived', message);
                }
                _mqttClient.connect({ keepAliveInterval: 15, customReconnectInterval: 10 });
                setMqttClient(_mqttClient);
              }
          }
          catch (error) {
            console.error('Erreur initialisation client MQTT', error);
          }
    }

    const disconnectBroker = () => {
        console.log("Disconnecting broker ...")
        if(mqttClient){
            try{
                if(mqttClient.isConnected)
                    mqttClient.disconnect();
                setMqttClient(undefined);
                console.log("Broker disconnected !")
            }
            catch (error) {
                console.error('Erreur initialisation client MQTT', error);
            }
        }
        else console.log("Cannot disconect broker : mqttClient is not defined");
        setTopicStatusName(undefined);
        setTopicDataName(undefined);
        setEncryptionKey(undefined);
        setBrokerStatus(2);
        exitFullScreen();
    }

    const publishMessage = (topic, message) => {
        if(mqttClient){
            try{
                console.log("Publishing message", topic, message);
                mqttClient.publish(topic, message);
            }
            catch (error) {
                console.error('Erreur client MQTT', error);
            }
        }
        else console.log("Cannot publish msg : mqttClient is not defined", topic, message);
    };

    const handleTakePictureBtnClick = () => {
        // console.log("handleTakePictureBtnClick");
        // const msg = "Publish msg ...";
        // publishMessage(topicDataName, msg);
        // return;
        goFullScreen();
        setProcessStep(PROCESS_STEP_IMAGE_CAPTURING);
    };

    const exitApp = () => {
        Dialog.show({
            content: 'Etes vous sûr de vouloir terminer ?',
            closeOnAction: true,
            actions: [
              [
                {
                    key: 'annuler',
                    text: 'Annuler',
                },
                {
                    key: 'terminer',
                    text: 'Terminer',
                    danger: true,
                    onClick: () => {
                        disconnectBroker();
                    },
                },
              ],
            ],
          });
    }

    const toggleFullscreen = () => {
        if(fullscreen){
            exitFullScreen();
        }
        else {
            goFullScreen();
        }
    }

    const goFullScreen = () => {
        if(!document.fullscreenElement){
            document.body.requestFullscreen();
            setFullscreen(true);
        }
    }

    const exitFullScreen = () => {
        if(document.fullscreenElement){
            document.exitFullscreen();
            setFullscreen(false);
        }
    }

    return (
      <div>
        <div className="main-window">

        <img src={logo} className="app-logo" alt="logo" />

        <div className='app-title'>
            MQTT media uploader
        </div>

        {
            (brokerStatus===0 || !mqttClient || !mqttClient.isConnected) && brokerStatus!==2 &&
            <NoticeBar
                color='alert'
                shape='neutral'
                bordered icon=''
                content="Broker non connecté !!!" />
        }

        {
            brokerStatus===1 &&
            <Grid columns={3} gap={6} className='footer-btns-container'>
                <Grid.Item>
                    <Button size='large' block className='app-btn' onClick={handleTakePictureBtnClick}>
                        <CameraOutline /><span className='label'>Prendre photo</span>
                    </Button>
                </Grid.Item>
                <Grid.Item>
                    <Button size='large' block className='app-btn'>
                        <UploadOutline /><span className='label'>Charger image</span>
                    </Button>
                </Grid.Item>
                <Grid.Item>
                    <Button size='large' block className='app-btn' onClick={exitApp}>
                    <CloseOutline color='var(--adm-color-danger)' /><span className='label'>Terminer</span>
                    </Button>
                </Grid.Item>
            </Grid>
        }

        {
            brokerStatus===2 &&
            <NoticeBar
                color='info' size="large"
                shape='neutral'
                bordered icon={<SmileOutline />}
                content="Merci, à bientot" />
        }

        </div>

        {
            processStep===PROCESS_STEP_IMAGE_CAPTURING &&
            <CameraView 
                onCancelBtnClick={()=>setProcessStep(undefined)}
                onImageCaptured={(imageData)=>setImageData(imageData)} />
        }

        {
            imageData &&
            <ImageView 
                imageData={imageData} uploadingImageData={uploadingImageData}
                onCancelBtnClick={()=>setImageData(undefined)}
                onSendBtnClick={()=>{
                    setUploadingImageData(true);
                    setTimeout(()=>{
                        // console.log("imageData", imageData);
                        const message = encryptionKey? aesEncrypt(imageData, encryptionKey) : imageData;
                        const imageId = uuidv7();
                        // publishMessage(topicDataName, message);                    
                        let charIndex = 0;             
                        let chunkIndex = 0;
                        const maxLength = 300000;
                        while (charIndex < message.length) {
                            let chunk = message.substring(charIndex, charIndex + maxLength);
                            publishMessage(topicDataName, imageId + '[#:#:#]' + chunkIndex + '[#:#:#]' + chunk); 
                            charIndex += maxLength; 
                            chunkIndex++;
                        }
                        publishMessage(topicDataName, imageId + '[#:#:#]' + chunkIndex); 
                        setTimeout(()=>{
                            setImageData(undefined);
                            setProcessStep(undefined);
                            setUploadingImageData(false);
                        }, 7000);
                    });
                }} />
        }

        <Button className='go-fullscreen-btn' onClick={toggleFullscreen}>
            { fullscreen? <ShrinkOutline /> : <ArrowsAltOutline /> }
        </Button>
      </div>
    );
  }
  
  export default DocumentCaptureView;